@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

.burger {
  position: fixed;
  right: 5vw;
  top: 3vh;
  z-index: 51;
  padding: 2px;
  border: none;
  cursor: pointer; }
  .burger .burger-img {
    height: 6vh;
    background-color: rgba(0, 0, 0, 0);
    transition: .3s; }
  .burger .close {
    height: 5vh;
    -webkit-filter: invert(1);
    filter: invert(1); }

.App {
  position: relative;
  left: 0;
  top: 0;
  background-repeat: repeat;
  background-size: cover;
  transition: .7s; }
  .App .background {
    position: fixed;
    left: 0;
    top: 0;
    height: 100vh;
    z-index: -50; }
    @media (min-width: 576px) {
      .App .background {
        width: 100vw; } }
  .App header {
    position: relative;
    height: 75vh; }
    @media (min-width: 576px) {
      .App header {
        height: 100vh; } }
    .App header nav div {
      height: 83px;
      background-image: linear-gradient(rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.1)); }
      .App header nav div .kangaroo {
        position: absolute;
        left: 5vw;
        top: 1.3vh;
        height: 60px; }
    .App header .main-logo {
      position: absolute;
      left: 50%;
      top: 46%;
      transform: translate(-50%, -50%);
      font-family: 'Permanent Marker', cursive;
      text-align: center;
      text-transform: uppercase;
      letter-spacing: 3px; }
      .App header .main-logo h1 {
        font-size: 25px; }
      .App header .main-logo h2 {
        font-size: 46px; }
  .App main .image-box {
    display: flex;
    width: 100%;
    border-radius: 20px;
    flex-wrap: wrap;
    justify-content: space-around;
    align-content: space-around;
    background-color: rgba(0, 0, 0, 0.8);
    padding: 10px 0; }
    @media (min-width: 576px) {
      .App main .image-box {
        flex-direction: column;
        flex-wrap: wrap; } }
    .App main .image-box div {
      padding: 10px 20px;
      flex-basis: 100%; }
      @media (min-width: 576px) {
        .App main .image-box div {
          flex-basis: 20%; } }
      @media (min-width: 768px) {
        .App main .image-box div {
          flex-basis: 33%; } }
      .App main .image-box div img {
        width: 100%; }
  .App section.about-me {
    position: relative;
    min-height: 100vh; }
    .App section.about-me .about-me-wrap {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      padding: 16px 13px 0;
      background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0));
      border-radius: 80px;
      width: 75vw;
      font-family: 'Permanent Marker', cursive;
      text-align: center;
      text-transform: uppercase;
      letter-spacing: 3px; }
      .App section.about-me .about-me-wrap .top h2 {
        font-size: 23px; }
      .App section.about-me .about-me-wrap .top h1 {
        font-size: 49px; }
      .App section.about-me .about-me-wrap .body {
        font-family: 'Permanent Marker', cursive;
        text-align: center;
        text-transform: uppercase;
        letter-spacing: 3px; }
  .App footer {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
    background-color: #000000; }
    .App footer p {
      margin-top: 12px;
      line-height: 12px;
      color: #6969696e;
      font-size: 9px;
      letter-spacing: 3px;
      text-transform: uppercase;
      font-family: Arial, "Helvetica Neue", Helvetica, sans-serif; }

.activeApp {
  position: relative;
  left: -85%;
  top: 0; }

ul.menu-hamburger {
  position: fixed;
  z-index: 50;
  left: 100%;
  top: 0;
  padding-top: 12vh;
  list-style: none;
  text-align: center;
  width: 85%;
  height: 100%;
  /* background-color: rgba(0, 0, 0, .87); */
  color: #ffffff;
  background-color: #000000;
  transition: .5s;
}

.kangaroo-menu {
  padding-bottom: 70px;
  -webkit-filter: invert(1);
  filter: invert(1);
}

.li {
  list-style: none;
  margin-bottom: 13vh;
  text-transform: uppercase;
  letter-spacing: 4px;
  font-family: 'Permanent Marker', cursive;
  font-size: 1.7rem;
  text-decoration: none !important;
  color: #ffffff !important;
  cursor: pointer;
}

.active {
  position: fixed;
  left: 15%;
  top: 0;
  padding-top: 12vh;
  list-style: none;
  text-align: center;
  width: 85%;
  height: 100%;
  color: #ffffff;
  background-color: rgba(0, 0, 0, .80);
  z-index: 50;
  transition: .5s;
}
